"use client";

import Image from "next/image";
import useEmblaCarousel from "embla-carousel-react";
import { useCallback } from "react";
import content from "../../content.json";

export default function HeroCarousel() {
  const [emblaRef, emblaApi] = useEmblaCarousel({ loop: true });

  const scrollPrev = useCallback(() => {
    if (emblaApi) emblaApi.scrollPrev();
  }, [emblaApi]);

  const scrollNext = useCallback(() => {
    if (emblaApi) emblaApi.scrollNext();
  }, [emblaApi]);

  return (
    <div className="relative w-full" ref={emblaRef}>
      {/* Wrapper */}
      <div className="flex">
        {content.hero.carousel.map((img: string, index: number) => (
          <div key={index} className="flex-[0_0_100%] flex justify-center">
            <Image
              src={img}
              alt={`Slide ${index + 1}`}
              width={600}
              height={100}
              className="rounded-lg object-contain"
            />
          </div>
        ))}
      </div>

      {/* Controles */}
      <button
        onClick={scrollPrev}
        className="absolute top-1/2 left-3 -translate-y-1/2 bg-black/30 text-white rounded-full px-3 py-1"
      >
        ‹
      </button>
      <button
        onClick={scrollNext}
        className="absolute top-1/2 right-3 -translate-y-1/2 bg-black/30 text-white rounded-full px-3 py-1"
      >
        ›
      </button>
    </div>
  );
}
